import React from 'react';
import ResultsHeaderThree from '../../ui/ResultsHeaderThree';
import ResultsHeaderThreeMix from '../../ui/ResultsHeaderThreeMix';
import Header from '../../ui/Header';
import { connect } from 'react-redux';
import { changeScreen, changeSelectedInboundService, changeSelectedOutboundService, changeSelectedOutboundTicket, changeSelectedInboundTicket } from '../../redux/actions';
import { getSelectedOutboundService, getSelectedOutboundTicket, getSelectedInboundService, getSelectedInboundTicket } from '../../utility/get';
import screens from '../../data/screen';

import serviceInfoScreen from '../../data/serviceInfo.png';
import termsAndConditionsScreen from '../../data/termsAndConditions.png';

import './ResultsThree.css';

import ResultsThreeServiceOutbound from './ResultsThreeServiceOutbound';
import ResultsThreeServiceInbound from './ResultsThreeServiceInbound';
import ResultsThreeTicketHighlights from './ResultsThreeTicketHighlights';
import ResultsThreeTicketOutbound from './ResultsThreeTicketOutbound';
import ResultsThreeTicketInbound from './ResultsThreeTicketInbound';
import ResultsThreePriceBreakdown from './ResultsThreePriceBreakdown';

const getCheapestTicketPrice = tickets => {
  const cheapest = Math.min(...tickets.map(ticket => ticket.price));
  return cheapest === Infinity ? null : cheapest;
};

const addDataToServices = services => {
  services.forEach(service => {
    if (service.tickets.length > 0) {
      service.price = {
        standard: getCheapestTicketPrice(service.tickets.filter(ticket => !ticket.isFirst)),
        first: getCheapestTicketPrice(service.tickets.filter(ticket => ticket.isFirst)),
      };
    } else {
      service.price = {
        standard: null,
        first: null,
      };
      service.note = 'No tickets available';
    }
    service.time.duration = service.time.arrival.diff(service.time.departure, 'minutes');
  });
}

const addQualifiers = services => {
  const cheapestServicePrice = Math.min(...services.map(service => Math.min(service.price.standard, service.price.first)));
  const fastestServiceTime = Math.min(...services.map(service => service.time.duration));
  
  services.forEach(service => {
    service.isCheapest = service.price.standard === cheapestServicePrice || service.price.first === cheapestServicePrice;
    service.isFastest = service.time.duration === fastestServiceTime;
  });
}

const mapStateToProps = state => {
  const { search, results } = state;
  const { outbound, inbound } = results;

  const passengers = parseInt(search.adults) + parseInt(search.children);

  if (outbound) {
    addDataToServices(outbound);
    addQualifiers(outbound);

    outbound.forEach(service => {
      service.passengers = passengers;
      service.tickets.forEach(ticket => ticket.passengers = passengers);
    });
  }

  if (inbound) {
    addDataToServices(inbound);
    addQualifiers(inbound);

    inbound.forEach(service => {
      service.passengers = passengers;
      service.tickets.forEach(ticket => ticket.passengers = passengers);
    });
  }

  return {
    results: state.results,
    servicesOutbound: outbound,
    servicesInbound: inbound,
    passengers: search.adults + search.children,
    selectedOutboundService: getSelectedOutboundService(state.results, state.selection),
    selectedOutboundTicket: getSelectedOutboundTicket(state.results, state.selection),
    selectedInboundService: getSelectedInboundService(state.results, state.selection),
    selectedInboundTicket: getSelectedInboundTicket(state.results, state.selection),
    search: state.search,
    screen: state.screen,
    selection: state.selection,
  };
};

const mapDispatchToProps = dispatch => ({
  changeScreen: id => dispatch(changeScreen(id)),
  selectOutboundService: id => dispatch(changeSelectedOutboundService(id)),
  selectInboundService: id => dispatch(changeSelectedInboundService(id)),
  selectOutboundTicket: id => dispatch(changeSelectedOutboundTicket(id)),
  selectInboundTicket: id => dispatch(changeSelectedInboundTicket(id)),
})

class ResultsThree extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      serviceInfo: false,
      termsAndConditions: false,
      priceBreakdownOpen: false,
      priceBreakdownOutId: null,
      priceBreakdownInId: null,
    };

    this.onServiceInfoClick = this.onServiceInfoClick.bind(this);
    this.onConditionsClick = this.onConditionsClick.bind(this);
    this.onPriceBreakdownClickHighlights = this.onPriceBreakdownClickHighlights.bind(this);
    this.onPriceBreakdownClickOutbound = this.onPriceBreakdownClickOutbound.bind(this);
    this.onPriceBreakdownClickInbound = this.onPriceBreakdownClickInbound.bind(this);
  }

  getResultsHeader() { 
    const { search, screen } = this.props;

    let title;
    let subtitle;

    if (screen === screens.serviceOutbound) {
      if (search.ticketType === 0) {
        title = 'Select service';
      } else {
        title = 'Select outbound service';
      }
      subtitle = search.outbound.time.format('ddd DD MMM');
    } else if (screen === screens.serviceInbound) {
      title = 'Select return service';
      subtitle = search.inbound.time.format('ddd DD MMM');
    } else if (screen === screens.ticketHighlights) {
      title = 'Choose tickets';
    } else if (screen === screens.ticketOutbound) {
      if (search.ticketType === 1) {
        return <ResultsHeaderThreeMix legNumber={0} outboundTime={search.outbound.time.format('ddd DD MMM')} inboundTime={search.inbound.time.format('ddd DD MMM')} />;
      } else {
        title = 'Choose ticket';
      }
    } else if (screen === screens.ticketInbound) {
      return <ResultsHeaderThreeMix legNumber={1} outboundTime={search.outbound.time.format('ddd DD MMM')} inboundTime={search.inbound.time.format('ddd DD MMM')} />;
    }

    return <ResultsHeaderThree title={title} subtitle={subtitle} />;
  }

  getHeader() { 
    const { search } = this.props;

    const data = {
      station: {
        origin: search.from,
        destination: search.to,
      },
      ticketType: search.ticketType,
    };

    return (
      <Header data={data} onBackClick={() => this.goToPreviousScreen()} />
    );
  }

  getPreviousScreenId() {
    const { screen, search } = this.props;

    switch (screen) {
      case screens.serviceOutbound:
        return screens.search;
      case screens.serviceInbound:
        return screens.serviceOutbound;
      case screens.ticketHighlights:
        if (search.ticketType === 1) {
          return screens.serviceInbound;
        } else {
          return screens.serviceOutbound;
        }
      case screens.ticketOutbound:
        return screens.ticketHighlights;
      case screens.ticketInbound:
        return screens.ticketOutbound;
      default:
        return screen - 1;
    }
  }

  goToPreviousScreen() {
    const { changeScreen } = this.props;

    changeScreen(this.getPreviousScreenId());
  }

  onScreenChange(id) {
    const { selectOutboundService, selectInboundService, selectOutboundTicket, selectInboundTicket } = this.props;

    switch (id) {
      case screens.serviceOutbound:
        selectOutboundService(null);
        return;
      case screens.serviceInbound:
        selectInboundService(null);
        return;
      case screens.ticketHighlights:
        selectInboundTicket(null);
        selectOutboundTicket(null);
        return;
      case screens.ticketOutbound:
        selectOutboundTicket(null);
        selectInboundTicket(null);
        return;
      case screens.ticketInbound:
        selectInboundTicket(null);
        return;
      default:
        return;
    }
  }

  componentDidUpdate(prevProps) {
    const { screen } = this.props;

    // Check did change screen
    if (prevProps.screen === screen) return;

    this.onScreenChange(screen);
  }

  onPriceBreakdownClickHighlights(idOut, idIn) {
    this.setState({
      priceBreakdownOpen: true,
      priceBreakdownOutId: idOut,
      priceBreakdownInId: idIn,
    });
  }

  onPriceBreakdownClickOutbound(id) {
    this.setState({
      priceBreakdownOpen: true,
      priceBreakdownOutId: id,
      priceBreakdownInId: null,
    });
  }

  onPriceBreakdownClickInbound(id) {
    const { selectedOutboundTicket } = this.props;

    this.setState({
      priceBreakdownOpen: true,
      priceBreakdownOutId: selectedOutboundTicket.id,
      priceBreakdownInId: id,
    });
  }

  onConditionsClick() {
    this.setState({
      termsAndConditions: true,
    });
  }

  onServiceInfoClick() {
    this.setState({
      serviceInfo: true
    });
  }

  getContent() {
    const { screen, servicesOutbound, servicesInbound } = this.props;

    switch (screen) {
      case screens.serviceOutbound:
        return <ResultsThreeServiceOutbound servicesOutbound={servicesOutbound} servicesInbound={servicesInbound} onServiceInfoClick={this.onServiceInfoClick} />;
      case screens.serviceInbound:
        return <ResultsThreeServiceInbound servicesOutbound={servicesOutbound} servicesInbound={servicesInbound} onServiceInfoClick={this.onServiceInfoClick} />;
      case screens.ticketHighlights:
        return <ResultsThreeTicketHighlights onPriceBreakdownClick={this.onPriceBreakdownClickHighlights} onConditionsClick={this.onConditionsClick} />;
      case screens.ticketOutbound:
        return <ResultsThreeTicketOutbound onPriceBreakdownClick={this.onPriceBreakdownClickOutbound} onConditionsClick={this.onConditionsClick} />;
      case screens.ticketInbound:
        return <ResultsThreeTicketInbound onPriceBreakdownClick={this.onPriceBreakdownClickInbound} onConditionsClick={this.onConditionsClick} />
      default:
        return 'Oh no';
    }
  }

  render() {
    const { serviceInfo, termsAndConditions, priceBreakdownOpen, priceBreakdownOutId, priceBreakdownInId } = this.state;

    return (
      <React.Fragment>
        <div className="Results">
          <div className={`${serviceInfo ? '' : 'hidden'}`}>
            <Header data={{ title: 'Service information' }} onBackClick={() => this.setState({ serviceInfo: false })} />
            <img src={serviceInfoScreen} className="Results-fakeContent" />
          </div>
          <div className={`${termsAndConditions ? '' : 'hidden'}`}>
            <Header data={{ title: 'Terms and conditions' }} onBackClick={() => this.setState({ termsAndConditions: false })} />
            <img src={termsAndConditionsScreen} className="Results-fakeContent" />
          </div>
          <div className={`${termsAndConditions || serviceInfo ? 'hidden' : ''}`}>
            {this.getHeader()}
            {this.getResultsHeader()}
            {this.getContent()}
          </div>
        </div>
        {priceBreakdownOpen && <div className="Results-modal" onClick={() => this.setState({ priceBreakdownOpen: false, priceBreakdownInId: null, priceBreakdownOutId: null })}>
          <ResultsThreePriceBreakdown priceBreakdownOutId={priceBreakdownOutId} priceBreakdownInId={priceBreakdownInId} />
        </div>}
      </React.Fragment>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResultsThree);
