import React from 'react';
import TicketResultList from '../../../ui/TicketResultList';
import screen from '../../../data/screen';
import { connect } from 'react-redux';
import { changeSelectedOutboundTicket, changeScreen } from '../../../redux/actions';
import { getSelectedOutboundService } from '../../../utility/get';

const mapStateToProps = state => ({
  ticketType: state.search.ticketType,
  selectedOutboundService: getSelectedOutboundService(state.results, state.selection),
});

const mapDispatchToProps = dispatch => ({
  changeScreen: id => dispatch(changeScreen(id)),
  selectOutboundTicket: id => dispatch(changeSelectedOutboundTicket(id)),
});

class ResultsThreeTicketOutbound extends React.Component {
  constructor(props) {
    super(props);

    this.onTicketSelect = this.onTicketSelect.bind(this);
  }

  onTicketSelect(id) {
    const { ticketType, selectedOutboundService } = this.props;
    const { selectOutboundTicket, changeScreen } = this.props;

    selectOutboundTicket(id);
    if (ticketType !== 1 || selectedOutboundService.tickets.find(ticket => ticket.id === id).isReturn) {
      changeScreen(screen.review);
    } else {
      changeScreen(screen.ticketInbound);
    }
  }

  render() {
    const { onPriceBreakdownClick, onConditionsClick } = this.props;
    const { selectedOutboundService } = this.props;

    const data = selectedOutboundService.tickets.map(ticket => (
      {
        id: ticket.id,
        title: ticket.name,
        price: ticket.price,
        descriptionLines: [],
        passengers: ticket.passengers,
        isDiscounted: ticket.isDiscounted,
      }
    ));

    return (
      <div className="Results-content">
        <TicketResultList data={data} onClick={this.onTicketSelect} onPriceBreakdownClick={onPriceBreakdownClick} onConditionsClick={onConditionsClick} />
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResultsThreeTicketOutbound);
