import React from 'react';
import ServiceResultList from '../../../ui/ServiceResultList';
import screen from '../../../data/screen';
import { connect } from 'react-redux';
import { changeSelectedInboundService, changeScreen } from '../../../redux/actions';
import { earlierInboundServices, laterInboundServices } from '../../../redux/actions';
import ResultsThreeServiceEarlier from '../ResultsThreeServiceEarlier';
import ResultsThreeServiceLater from '../ResultsThreeServiceLater';
import { getSelectedOutboundService } from '../../../utility/get';

const mapStateToProps = state => ({
  selectedOutboundService: getSelectedOutboundService(state.results, state.selection),
});

const mapDispatchToProps = dispatch => ({
  changeScreen: id => dispatch(changeScreen(id)),
  selectInboundService: id => dispatch(changeSelectedInboundService(id)),
  earlierInboundServices: () => dispatch(earlierInboundServices()),
  laterInboundServices: () => dispatch(laterInboundServices()),
});

class ResultsThreeServiceInbound extends React.Component {
  constructor(props) {
    super(props);

    this.onServiceSelect = this.onServiceSelect.bind(this);
  }

  onServiceSelect(id) {
    const { changeScreen, selectInboundService } = this.props;

    selectInboundService(id);
    changeScreen(screen.ticketHighlights);
  }

  render() {
    const { servicesInbound, onServiceInfoClick } = this.props;
    const { selectedOutboundService, laterInboundServices, earlierInboundServices } = this.props;

    const presentationServices = [];
    
     // Adjust prices based on selected outbound service
    servicesInbound.forEach(service => {
      const outboundReturns = selectedOutboundService.tickets.filter(ticket => ticket.isReturn);
      const cheapestOutboundReturnStandard = Math.min(...outboundReturns.filter(ticket => !ticket.isFirst).map(ticket => ticket.price));
      const cheapestOutboundReturnFirst = Math.min(...outboundReturns.filter(ticket => ticket.isFirst).map(ticket => ticket.price));
      // Consider whether a return is cheaper than two singles
      presentationServices.push({
        ...service,
        price: {
          standard: Math.min(selectedOutboundService.price.standard + service.price.standard, cheapestOutboundReturnStandard),
          first: Math.min(selectedOutboundService.price.first + service.price.first, cheapestOutboundReturnFirst),
        },
      });
    });

    return (
      <div className="ResultsThreeService">
        <ResultsThreeServiceEarlier onClick={earlierInboundServices} />
        <ServiceResultList data={presentationServices} onClick={this.onServiceSelect} onServiceInfoClick={onServiceInfoClick}/>
        <ResultsThreeServiceLater onClick={laterInboundServices} />
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResultsThreeServiceInbound);
