import React from 'react';
import ServiceResult from './ServiceResult';

import './ServiceResultList.css';

export default class ServiceResultList extends React.Component {
  render() {
    const { data, onClick, onServiceInfoClick } = this.props;

    return (
      <div className="ServiceResultList">
        {data.map(item => {
          return (
            <ServiceResult key={item.id} data={item} onClick={() => onClick(item.id)} onServiceInfoClick={() => onServiceInfoClick(item.id)} />
          );
        })}
      </div>
    );
  }
}
