import React from 'react';
import TicketResultList from '../../../ui/TicketResultList';
import screen from '../../../data/screen';
import { connect } from 'react-redux';
import { changeSelectedOutboundTicket, changeSelectedInboundTicket, changeScreen } from '../../../redux/actions';
import { getSelectedInboundService } from '../../../utility/get';

const mapStateToProps = state => ({
  selectedInboundService: getSelectedInboundService(state.results, state.selection),
});

const mapDispatchToProps = dispatch => ({
  changeScreen: id => dispatch(changeScreen(id)),
  selectOutboundTicket: id => dispatch(changeSelectedOutboundTicket(id)),
  selectInboundTicket: id => dispatch(changeSelectedInboundTicket(id)),
})

class ResultsThreeTicketInbound extends React.Component {
  constructor(props) {
    super(props);

    this.onTicketSelect = this.onTicketSelect.bind(this);
  }

  onTicketSelect(id) {
    const { selectInboundTicket, changeScreen } = this.props;

    selectInboundTicket(id);
    changeScreen(screen.review);
  }

  render() {
    const { onPriceBreakdownClick, onConditionsClick } = this.props;
    const { selectedInboundService } = this.props;

    const data = selectedInboundService.tickets.map(ticket => (
      {
        id: ticket.id,
        title: ticket.name,
        price: ticket.price,
        descriptionLines: [],
        passengers: ticket.passengers,
        isDiscounted: ticket.isDiscounted,
      }
    ));

    return (
      <div className="Results-content">
        <TicketResultList data={data} onClick={this.onTicketSelect} onPriceBreakdownClick={onPriceBreakdownClick} onConditionsClick={onConditionsClick} />
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResultsThreeTicketInbound);
