import React from 'react';
import ServiceResultList from '../../../ui/ServiceResultList';
import screen from '../../../data/screen';
import { connect } from 'react-redux';
import { changeSelectedOutboundService, changeScreen } from '../../../redux/actions';
import { earlierOutboundServices, laterOutboundServices } from '../../../redux/actions';

import ResultsThreeServiceEarlier from '../ResultsThreeServiceEarlier';
import ResultsThreeServiceLater from '../ResultsThreeServiceLater';

const mapStateToProps = null;

const mapDispatchToProps = dispatch => ({
  changeScreen: id => dispatch(changeScreen(id)),
  selectOutboundService: id => dispatch(changeSelectedOutboundService(id)),
  earlierOutboundServices: () => dispatch(earlierOutboundServices()),
  laterOutboundServices: () => dispatch(laterOutboundServices()),
})

class ResultsThreeServiceOutbound extends React.Component {
  constructor(props) {
    super(props);

    this.onServiceSelect = this.onServiceSelect.bind(this);
  }

  onServiceSelect(id) {
    const { servicesInbound } = this.props;
    const { changeScreen, selectOutboundService } = this.props;

    selectOutboundService(id);
    if (servicesInbound) {
      changeScreen(screen.serviceInbound);
    } else {
      changeScreen(screen.ticketHighlights);
    }
  }

  render() {
    const { servicesOutbound, servicesInbound, onServiceInfoClick } = this.props;
    const { earlierOutboundServices, laterOutboundServices } = this.props;

    const presentationServices = [];

    // In return case, adjust prices to be full journey
    // i.e. if there are inbound journeys
    if (servicesInbound) {
      // Calculate minima from potential inbound tickets
      const inboundTickets = [].concat(...servicesInbound.map(service => service.tickets));
      const inboundSingles = inboundTickets.filter(ticket => !ticket.isReturn);
      const cheapestInboundLegStandard = Math.min(...inboundSingles.filter(ticket => !ticket.isFirst).map(ticket => ticket.price));
      const cheapestInboundLegFirst = Math.min(...inboundSingles.filter(ticket => ticket.isFirst).map(ticket => ticket.price));
     
      // For each service...
      servicesOutbound.forEach(service => {
        const outboundReturns = service.tickets.filter(ticket => ticket.isReturn);
        const cheapestOutboundReturnStandard = Math.min(...outboundReturns.filter(ticket => !ticket.isFirst).map(ticket => ticket.price));
        const cheapestOutboundReturnFirst = Math.min(...outboundReturns.filter(ticket => ticket.isFirst).map(ticket => ticket.price));
        // Consider whether a return is cheaper than two singles
        presentationServices.push({
          ...service,
          price: {
            standard: Math.min(service.price.standard + cheapestInboundLegStandard, cheapestOutboundReturnStandard),
            first: Math.min(service.price.first + cheapestInboundLegFirst, cheapestOutboundReturnFirst),
          },
        });
      });
    } else {
      servicesOutbound.forEach(service => {
        presentationServices.push({
          ...service,
        });
      });
    };

    return (
      <div className="ResultsThreeServiceOutbound">
        <ResultsThreeServiceEarlier onClick={earlierOutboundServices} />
        <ServiceResultList data={presentationServices} onClick={this.onServiceSelect} onServiceInfoClick={onServiceInfoClick} />
        <ResultsThreeServiceLater onClick={laterOutboundServices} />
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResultsThreeServiceOutbound);
